import React from "react";
import {
    BrowserRouter as Router,
    Route,
    Switch,
    Redirect,
} from 'react-router-dom';

const AuthRoute = ({ component: Component, authUser, ...rest }) => {
    return (
        <Route
            {...rest}
            render={(props) =>
                authUser.user ? (
                    <Component {...props} />
                ) : (
                    <Redirect
                        to={{
                            pathname: '/user/login',
                            state: { from: props.location },
                        }}
                    />
                )
            }
        />
    );
};

export default AuthRoute