import React from "react";
import { BASE_URL } from "../../api/settings";

class RedirectPage extends React.Component<any, any> {
    componentDidMount() {
        const query = new URLSearchParams(this.props.location.search);
        var url = query.get("url");
        if (url) {
            window.location.replace(url);
        }
    }

    render() {
        return (<div style={{ width: "100vw", height: "100vh", display: "flex", justifyContent: "center", alignItems: "center" }}><div className="loading" /></div>)
    }
}

export default RedirectPage