import React, { Suspense } from 'react';
import { connect } from 'react-redux';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import { IntlProvider } from 'react-intl';
import AppLocale from './lang';
import ColorSwitcher from './components/common/ColorSwitcher';
import { NotificationContainer } from './components/common/react-notifications';
import { isMultiColorActive } from './constants/defaultValues';
import { getDirection } from './helpers/Utils';
import AuthRoute from "./components/authRoute";
import RedirectPage from './views/redirectPage/RedirectPage';


const ViewMain = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views')
);

const ViewUser = React.lazy(() =>
  import(/* webpackChunkName: "views-user" */ './views/user')
);

const ViewDashboard = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views/dashboards')
);

const ViewPhotoOrder = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views/photoOrder')
);

const ViewPhotoHosting = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views/photoHosting')
);


const ViewSettings = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views/settings')
);

const ViewPortal = React.lazy(() =>
  import(/* webpackChunkName: "views" */ './views/portal')
);

const ViewError = React.lazy(() =>
  import(/* webpackChunkName: "views-error" */ './views/error')
);

class App extends React.Component {
  constructor(props) {
    super(props);
    const direction = getDirection();
    if (direction.isRtl) {
      document.body.classList.add('rtl');
      document.body.classList.remove('ltr');
    } else {
      document.body.classList.add('ltr');
      document.body.classList.remove('rtl');
    }
  }

  render() {
    const { locale } = this.props;
    const currentAppLocale = AppLocale[locale];

    return (
      <div className="h-100">
        <IntlProvider
          locale={currentAppLocale.locale}
          messages={currentAppLocale.messages}
          onError={() => { }}
        >
          <>
            <NotificationContainer />
            {isMultiColorActive && <ColorSwitcher />}
            <Suspense fallback={<div className="loading" />}>
              <Router>
                <Switch>
                  <AuthRoute
                    path="/dashboard"
                    authUser={this.props.authUser}
                    component={ViewDashboard}
                  />

                  <Route
                    path={`/photo-hosting`}
                    // authUser={this.props.authUser}
                    // component={ViewPhotoHosting}
                    render={(props) => <ViewPhotoHosting authUser={this.props.authUser} {...props} />}
                  />

                  <AuthRoute
                    path="/photo-order"
                    authUser={this.props.authUser}
                    component={ViewPhotoOrder}
                  />

                  <Route
                    path="/user"
                    render={(props) => <ViewUser {...props} />}
                  />

                  <Route
                    path="/redirect"
                    render={(props) => <RedirectPage {...props} />}
                  />

                  <Route
                    path="/portal"
                    render={(props) => <ViewPortal {...props} />}
                  />

                  <AuthRoute
                    path="/settings"
                    authUser={this.props.authUser}
                    component={ViewSettings}
                  />


                  <Route
                    path="/error"
                    exact
                    render={(props) => <ViewError {...props} />}
                  />


                  <AuthRoute
                    path="/"
                    authUser={this.props.authUser}
                    component={ViewMain}
                  />


                  <Redirect to="/error" />
                </Switch>
              </Router>
            </Suspense>
          </>
        </IntlProvider>
      </div>
    );
  }
}

const mapStateToProps = ({ settings, authUser }) => {
  const { locale } = settings;
  return { locale, authUser };
};
const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(App);
